/**
 * Internal dependencies
 */

import { applyFilters } from '@wordpress/hooks'
import Block from './block'
import renderFrontend from '../../util/render-frontend.js'

const getProps = el => ( {
	attributes: JSON.parse( el.dataset.attributes ),
	hasVisibilitySensor: applyFilters( 'cartzillagb.products.list.disable.visibility.sensor', true ),
} )

renderFrontend(
	'.wp-block-czgb-products-list',
	Block,
	getProps
)
